* {
  padding: 0;
  margin: 0;
}

body {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}

.container {
  max-width: 700px;
  margin: 0 auth;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  padding:  1.2rem 0;
}

header p {
  color:rgb(255, 0, 0);
  font-size: 5rem;
  font-weight: 1000;
}

.txttwin {
  color:rgb(55, 0, 255);
}

.tutwin {
  color:rgb(255, 0, 0);
  font-size: 4rem;
}

.tulwin {
  color:rgb(0, 0, 0);
  font-size: 4rem;
}
header ul {
  display: flex;
}
header ul li {
  list-style-type: none;
  margin-left: 1.5rem;
}
header ul li a {
  text-decoration: none;

}

section {
  text-align: center;
  margin-bottom: 3rem;
}
section p {
  margin-bottom: 1.5rem;
  font-size: 2rem;
  font-weight: 600;
}

.profff{
  font-size: 1.3rem;
}

.prophoto {
  margin: 2rem;
  border-radius: 25%;
}

.prodesc {
  width: 200px;
  margin: 0 auto;
  margin-bottom: 2rem;
}

section[id="st1"] ul {
  padding: 5px;
}
section[id="st1"] ul li {
  display: flex;
  color:rgb(0, 0, 0);
}
section[id="st2"] ul {
  padding: 5px;
}
section[id="st2"] ul li {
  display: flex;
  color:rgb(0, 0, 0);
}
.proessay {
  font-size: 1.2rem;
  max-width: 360px;
  margin: 0 auto;
  line-height: 1.8rem;
  letter-spacing:  2px;
  word-spacing: 1px;
  text-indent: 10px;
  text-align: left;
}